@keyframes pulse {
  0% {
    transform: scale(1);
    border-color: #6c7a5d;
  }

  100% {
    transform: scale(1.2);
    border-color: transparent;
  }
}

.home {
  padding: 56px 24px 48px;
  position: relative;
  height: 100vh;
  background-color: #ffffff;
  background-image: url('../../assets/svgs/home.svg');
  transform: translateZ(0);

  .content {
    display: flex;
    justify-content: center;
    max-width: 1400px;
    height: 100%;
    margin: 0 auto;

    .intro {
      padding-left: 28px;
      padding-top: 8vh;

      .like {
        // typed.js 的光标样式
        & + span {
          color: #6c7a5d !important;
        }
      }

      p {
        letter-spacing: 5px;
        text-align: left;
        font-size: 65px;
        font-weight: 600;
        color: #002245;
        line-height: 1.5;
      }
    }

    .ballWrapper {
      // padding: 10px;
      margin-top: 60px;
      display: flex;
      align-items: center;
      justify-content: center;

      .ball {
        position: relative;
        width: 400px;
        height: 400px;
        background: rgba($color: #6c7a5d, $alpha: 0.8);
        border-radius: 50%;
        transition: all 1s;

        div {
          width: 95%;
          height: 95%;
          border-radius: 50%;
          align-items: center;
        }

        &::before {
          content: '';
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          width: 100%;
          height: 100%;
          animation: pulse 2s cubic-bezier(0.57, 0.06, 0.27, 0.84) infinite;
          border: 4px solid #6c7a5d;
          border-radius: 50%;
          background: transparent;
          z-index: 1;
        }
      }
    }

    .next {
      display: flex;
      align-items: center;
      justify-content: center;

      .handDown {
        > div {
          position: relative;
          display: inline-flex;
          justify-content: center;
          align-items: center;
          height: 100px;
          width: 100px;
          color: #6c7a5d;
          cursor: pointer;

          &::after {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            bottom: 0;
            right: 0;
            border-radius: 50%;
            transform: scale(0);
            transition: all 0.3s;
          }

          &:hover {
            &::after {
              background-color: rgba($color: #6c7a5d, $alpha: 0.8);
              transform: scale(1.1);
              z-index: -1;
            }
          }
        }
      }
    }
  }

  @media screen and (max-width: 992px) {
    .content {
      .intro {
        padding-top: 4vh;

        p {
          font-size: 2em;
        }

        .del {
          &::before,
          &::after {
            top: 50%;
          }
        }
      }

      .ballWrapper {
        padding: 0;

        .ball {
          height: 200px;
          width: 200px;
          font-size: 5em;
        }
      }
    }
  }

  @media screen and (max-width: 768px) {
    .content {
      .intro {
        padding-left: 0;
        padding-top: 10px;

        p {
          line-height: 1.4em;
          font-size: 1.7em;
        }
        .del {
          &::before,
          &::after {
            top: 50%;
          }
        }
      }
    }
  }
}
